<template>
    <div>
        <div v-if="mode === 'DESIGN'">
            <el-input size="medium" disabled :placeholder="placeholder" type="number"/>
<!--            <el-input-number style="width: 100%" disabled></el-input-number>-->
        </div>
        <div v-else>
            <el-input :disabled="perm&&perm==='R'" v-model.number="_value" size="medium"
                      :clearable="!perm||perm==='E'"
                      :placeholder="placeholder" type="number"/>

<!--            <el-input-number style="width: 100%"  :disabled="perm&&perm==='R'" v-model="_value" :precision="precision" :min="minNum" :max="maxNum"></el-input-number>-->
        </div>
    </div>
</template>

<script>
import componentMinxins from '../ComponentMinxins'

export default {
    mixins: [componentMinxins],
    name: "NumberInput",
    components: {},
    props: {
        value: {
            type: Number,
            default: null
        },
        precision: {
            type: Number,
            default: null
        },
        minNum: {
            type: Number,
            default: null
        },
        maxNum: {
            type: Number,
            default: null
        },
        placeholder: {
            type: String,
            default: '请输入数值'
        },
        perm: {
            type: String,
            default: 'E'
        }
    },
    watch: {
        perm: function (v) {
            console.log('perm', v)
        }
    },
    data() {
        return {}
    },
    mounted() {
    },
    methods: {}
}
</script>

